.sidebar-card {
  background: #FFFFFF;
  border: 1px solid #EAEAEA;
  font-weight: 500;
  font-size: 17px;
  color: #0D1B2A;
  letter-spacing: 0;
  border-radius: 4px;
  padding: 15px 30px 28px 30px;
  margin: 0 0 30px;
}
.sidebar-card .row {
  margin-top: 20px;
  position: relative;
}
.sidebar-card .row .right {
  text-align: right;
}
.sidebar-card .row .center {
  text-align: center;
}
.sidebar-card .row .row-head {
  font-weight: 600;
  font-size: 8.8px;
  color: #989CA8;
  letter-spacing: 0;
}
.sidebar-card .row .row-body-light {
  font-weight: 600;
  font-size: 13.6px;
  color: #989CA8;
  letter-spacing: 0;
}
.sidebar-card .row .row-body-dark {
  font-weight: 400;
  font-size: 12.8px;
  color: #0d1b2a;
  letter-spacing: 0;
}
