.news-row:before {
  content: ' ';
  width: 1px;
  height: calc(80%);
  background: #0053FA;
  position: absolute;
  left: 11px;
  top: 33px;
}
.news-row:last-child:before {
  content: none;
}
.news-row svg {
  margin: 5px 8px 0 0;
  width: 20px;
  height: 20px;
}
.news-row .value {
  font-weight: 500;
  font-size: 14px;
  color: #0D1B2A;
  letter-spacing: 0;
}
.news-row .through {
  font-weight: 500;
  font-size: 12px;
  color: #989CA8;
  letter-spacing: 0;
}
.news-row .date {
  font-weight: 600;
  font-size: 12px;
  color: #989CA8;
  letter-spacing: 0;
}
